.main-page-title {
    margin-bottom: 4rem;
    text-align: center;
    padding: 0px 20px;
    background: #1947ba;
    display: inline-block;
    text-transform: uppercase;
}

.about-content{
    margin: 50px 0;
    @include breakpoint(xsmall){
        margin: 30px 0;
    }
p{
    color: #fff;
    font-family: "Helvetica Neue";
    font-style: normal;
    font-weight: 700;
    font-size: 19px;
    line-height: 31px;
    @include breakpoint(xsmall){
        text-align: center;
    }
    @include breakpoint(small){
        text-align: left;
    }

}
}

.lfimg{
   margin:  50px 0;
   @include breakpoint(xsmall){
    margin: 30px 0;
}
}

.left-about-content{
    margin-right: 20px;
}
.right-about-content {
    margin-left: 20px;
}