@import url("http://fonts.cdnfonts.com/css/helvetica-neue-9");
@import url("http://fonts.cdnfonts.com/css/akira-expanded");
@font-face {
  font-family: "Jost";
  src: url("jost-v14-latin-regular.eot");
  src: url("jost-v14-latin-regular.woff") format("woff"), url("jost-v14-latin-regular.ttf") format("truetype"), url("jost-v14-latin-regular.svg") format("svg");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  src: url("GT-Super-Text-Black.woff2") format("woff");
  font-family: "GT Super Txt Trial";
  font-style: normal;
  font-weight: 700;
}
@font-face {
  src: url("GT-Super-Text-Bold.woff2") format("woff");
  font-family: "GT Super Txt Trial";
  font-style: normal;
  font-weight: 600;
}
@font-face {
  src: url("GT-Super-Text-Medium.woff2") format("woff");
  font-family: "GT Super Txt Trial";
  font-style: normal;
  font-weight: 500;
}
@font-face {
  src: url("GT-Super-Text-Regular.woff2") format("woff");
  font-family: "GT Super Txt Trial";
  font-style: normal;
  font-weight: 600;
}
body {
  font-family: "Jost";
  font-style: normal;
}

.img {
  transition: all 0.3s ease-out 0s;
}

.f-left {
  float: left;
}

.f-right {
  float: right;
}

.fix {
  overflow: hidden;
}

a,
.button {
  -webkit-transition: all 0.3s ease-out 0s;
  -moz-transition: all 0.3s ease-out 0s;
  -ms-transition: all 0.3s ease-out 0s;
  -o-transition: all 0.3s ease-out 0s;
  transition: all 0.3s ease-out 0s;
}

a:focus,
.button:focus {
  text-decoration: none;
  outline: none;
}

a,
button {
  color: #1696e7;
  outline: medium none;
}

button:focus, input:focus, input:focus, textarea, textarea:focus {
  outline: 0;
}

.uppercase {
  text-transform: uppercase;
}

.capitalize {
  text-transform: capitalize;
}

a:hover {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #373737;
  font-family: "GT Super Txt Trial";
  font-style: normal;
  font-weight: 700;
}

h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
  color: inherit;
}

ul {
  margin: 0px;
  padding: 0px;
}

li {
  list-style: none;
}

p {
  font-family: "Jost";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  color: #000;
  margin-bottom: 0px;
}

hr {
  border-bottom: 1px solid rgba(0, 0, 0, 0.4);
  border-top: 0 none;
  margin: 18px 0;
}

label {
  cursor: pointer;
  font-size: 18px;
  font-family: "Jost";
  font-style: normal;
  font-weight: 400;
}

option {
  font-family: "Jost";
  font-style: normal;
  font-weight: 400;
}

*::-moz-selection {
  background: #d6b161;
  color: #fff;
  text-shadow: none;
}

::-moz-selection {
  background: #444;
  color: #fff;
  text-shadow: none;
}

::selection {
  background: #444;
  color: #fff;
  text-shadow: none;
}

*::-moz-placeholder {
  color: #555555;
  font-size: 14px;
  opacity: 1;
}

*::placeholder {
  color: #555555;
  font-size: 14px;
  opacity: 1;
}

.theme-overlay {
  position: relative;
}

.theme-overlay::before {
  background: #EA5B0C;
  content: "";
  height: 100%;
  left: 0;
  opacity: 0.6;
  position: absolute;
  top: 0;
  width: 100%;
}

.separator {
  border-top: 1px solid #f2f2f2;
}

/* button style */
.btn {
  -moz-user-select: none;
  background: #fff;
  color: #000;
  cursor: pointer;
  display: inline-block;
  font-size: 16px;
  font-weight: 500;
  border-radius: 0px;
  letter-spacing: 1px;
  line-height: 1;
  margin-bottom: 0;
  padding: 15px 20px;
  text-align: center;
  text-transform: matrix(-1, 0, 0, 1, 0, 0);
  touch-action: manipulation;
  transition: all 0.3s ease 0s;
  vertical-align: middle;
  white-space: nowrap;
  font-size: 16px;
  border-radius: 30px;
  border: none;
  border-color: #000;
  text-decoration: inherit;
  font-family: system-ui;
  border-image-slice: 1;
}

.btn:hover {
  color: #000;
}

.breadcrumb > .active {
  color: #888;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
  display: inline-block;
}

a {
  text-decoration: none;
  font-weight: 500;
  font-family: "Jost";
  font-size: 14px;
  color: #545151;
}
a:hover {
  color: #000;
}

img {
  vertical-align: middle;
  border-style: none;
  max-width: 100%;
}

div#backToCollection {
  margin: 40px 0;
}

.main-page-title {
  margin-bottom: 4rem;
  text-align: center;
  padding: 0px 20px;
  background: #1947ba;
  display: inline-block;
  text-transform: uppercase;
}

.about-content {
  margin: 50px 0;
}
@media (min-width: 250px) {
  .about-content {
    margin: 30px 0;
  }
}
.about-content p {
  color: #fff;
  font-family: "Helvetica Neue";
  font-style: normal;
  font-weight: 700;
  font-size: 19px;
  line-height: 31px;
}
@media (min-width: 250px) {
  .about-content p {
    text-align: center;
  }
}
@media (min-width: 576px) {
  .about-content p {
    text-align: left;
  }
}

.lfimg {
  margin: 50px 0;
}
@media (min-width: 250px) {
  .lfimg {
    margin: 30px 0;
  }
}

.left-about-content {
  margin-right: 20px;
}

.right-about-content {
  margin-left: 20px;
}

.contact-btn {
  font-family: "Akira Expanded", sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 30px;
  color: #FFFFFF;
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .singel-content {
    margin: 25px 0 30px 0px;
  }
}
@media (min-width: 992px) {
  .singel-content {
    margin: 25px 0 30px 70px;
  }
}
.singel-content h3 {
  font-family: "Akira Expanded", sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 45px;
  line-height: 52px;
  color: white;
  margin-bottom: 30px;
}
.singel-content p {
  color: #fff;
  font-family: "Helvetica Neue";
  font-style: normal;
  font-weight: 500;
  font-size: 25px;
  line-height: 43px;
}
.singel-content p a {
  color: #fff;
  font-family: "Helvetica Neue";
  font-style: normal;
  font-weight: 500;
  font-size: 25px;
  line-height: 43px;
}