// Responsive Mixin 
@mixin breakpoint($point) {

    // 576 to Upper
    @if $point == hhxsmallm {
        @media (min-width: 240px) and (max-width: 500px) { 
            @content ; 
        }
    }
        
    // 576 to Upper
    @if $point == xsmallm {
        @media (min-width: 250px) and (max-width: 575px) { 
            @content ; 
        }
    }


    // 576 to Upper
    @if $point == smallm {
        @media (min-width: 576px) and (max-width: 767px) { 
            @content ; 
        }
    }

    // 768 to Upper
    @if $point == mediumm {
        @media (min-width: 768px) and (max-width: 991px){ 
            @content ; 
        }
    }

    // 998 to Upper
    @if $point == largem {
        @media (min-width: 992px) and (max-width: 1199px) { 
            @content ; 
        }
    }
    @if $point == shoplargem {
        @media (min-width: 1200px) and (max-width: 1299px) { 
            @content ; 
        }
    }

    // 1200 to Upper
    @if $point == xlargem {
        @media (min-width: 1200px) and (max-width: 1399px) { 
            @content ; 
        }
    }
    // 576 to Upper
    @if $point == hhxsmall {
        @media (min-width: 240px) and (max-width: 500px) { 
            @content ; 
        }
    }
        
    // 576 to Upper
    @if $point == xsmall {
        @media (min-width: 250px)  { 
            @content ; 
        }
    }


    // 576 to Upper
    @if $point == small {
        @media (min-width: 576px)  { 
            @content ; 
        }
    }

    // 768 to Upper
    @if $point == medium {
        @media (min-width: 768px) { 
            @content ; 
        }
    }

    // 998 to Upper
    @if $point == large {
        @media (min-width: 992px)  { 
            @content ; 
        }
    }

    // 1200 to Upper
    @if $point == xlarge {
        @media (min-width: 1200px) { 
            @content ; 
        }
    }

    // 1400 to Upper
    @if $point == xxlarge {
        @media (min-width: 1400px) { 
            @content ; 
        }
    }

    // 1800 to Upper
    @if $point == xxxlarge {
        @media (min-width: 1800px) { 
            @content ; 
        }
    }

}